/* Base styles */
.contact-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px;
  padding-right: 360px;
  margin: 0 auto;
  width: 100%;
  height: 100vh;
  max-width: 1200px;
  position: relative;
}

.contact-header {
  position: absolute;
  top: 74px;
  left: 270px;
  text-align: left;
}

.contact-button {
  display: flex;
  align-items: center;
  background: black;
  color: white;
  padding: 5px 30px;
  border-radius: 20px;
  width: fit-content;
  margin-bottom: 20px;
  font-family: 'Poppins', sans-serif;
  font-size: 0.9rem;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

.contact-button:hover {
  background-color: #333;
  transform: scale(1.05);
}

.contact-icon {
  margin-right: 5px;
}

.contact-title {
  font-size: 36px;
  font-weight: bold;
  margin: 20px 0 10px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.contact-title .highlight {
  color: #7e57c2;
}

.underline {
  height: 4px;
  width: 150px;
  background-color: #7e57c2;
  border-radius: 2px;
  margin-left: 0;
}

.contact-form-container {
  margin-left: 360px;
  margin-top: 10px;
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4);
  width: 100%;
  max-width: 400px;
  padding: 20px;
  text-align: center;
}

.form-title {
  font-size: 24px;
  font-weight: bold;
  color: #333;
  margin-bottom: 10px;
}

.form-subtitle {
  font-size: 14px;
  color: #555;
  margin-bottom: 20px;
}

.contact-form {
  display: flex;
  flex-direction: column;
}

.contact-form label {
  font-size: 14px;
  color: #333;
  margin-bottom: 5px;
  text-align: left;
}

.contact-form input,
.contact-form textarea {
  font-size: 14px;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid black;
  border-radius: 5px;
  width: 100%;
  background-color: white;
  box-sizing: border-box;
}

.contact-form input {
  height: 45px;
}

.contact-form textarea {
  resize: none;
  height: 120px;
}

.submit-button {
  background-color: black;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  height: 45px;
  width: 100%;
  transition: background-color 0.3s;
}

.submit-button:hover {
  background-color: #333;
}

.status-message {
  margin-top: 15px;
  font-size: 14px;
  color: #333;
}

/* Mobile Styles */
@media screen and (max-width: 768px) {
  .contact-container {
    padding: 20px;
    height: auto;
    min-height: 100vh;
  }

  .contact-header {
    position: relative;
    top: 0;
    left: 0;
    margin-bottom: 40px;
    width: 100%;
  }

  .contact-title {
    font-size: 28px;
  }

  .contact-button {
    padding: 5px 20px;
    font-size: 0.8rem;
  }

  .underline {
    width: 120px;
  }

  .contact-form-container {
    margin-left: 0;
    margin-top: 0;
    width: 100%;
    max-width: none;
    padding: 20px;
  }

  .form-title {
    font-size: 20px;
  }

  .form-subtitle {
    font-size: 13px;
    padding: 0 10px;
  }

  .contact-form input,
  .contact-form textarea {
    font-size: 16px;
  }

  .contact-form label {
    font-size: 14px;
  }

  .submit-button {
    font-size: 15px;
    height: 42px;
  }

  .status-message {
    font-size: 13px;
    padding: 0 10px;
  }
}