/* General Styles */
.skills-section {
  font-family: 'Arial', sans-serif;
  background-color: #f9f9f9;
  padding: 10px;
  border-radius: 8px;
  text-align: left;
  padding-top: 200px;
  padding-left: 90px;
}

.skills-header {
  margin-bottom: 10px;
}

.skills-button {
  background-color: black;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 5px;
  font-size: 14px;
  cursor: pointer;
}

.skills-title {
  font-size: 36px;
  font-weight: bold;
  color: #333;
  margin: 20px 0;
  text-align: left;
}

.skills-title .highlight {
  color: #7e57c2;
}

.skills-underline {
  height: 10px;
  width: 80px;
  background-color: #7e57c2;
  margin: 10px 0 20px;
  border-radius: 2px;
}

.skills-categories {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.skills-category {
  display: flex;
  align-items: center;
  width: 100%;
}

.skills-category h2 {
  font-size: 18px;
  color: #333;
  margin: 0;
  flex-shrink: 0;
  min-width: 120px;
  margin-right: 10px;
  text-align: left;
}

.skills-icons {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.skills-icons img {
  width: 30px;
  height: 30px;
  object-fit: contain;
  border-radius: 5px;
  transition: transform 0.2s ease;
}

.skills-icons img:hover {
  transform: scale(1.1);
  border: 2px solid #7e57c2;
}

.animation-container {
  position: absolute;
  right: 55px;
  top: 55%;
  transform: translateY(-50%);
  width: 600px;
  height: auto;
}

/* Input and Textarea Styles */
input[type="text"], textarea {
  font-family: 'Arial', sans-serif;
  font-size: 14px;
  color: black;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  box-sizing: border-box;
}

input[type="text"]:focus, textarea:focus {
  outline: none;
  border-color: #7e57c2;
  box-shadow: 0 0 5px #7e57c2;
}

/* Mobile Styles */
@media screen and (max-width: 768px) {
  .skills-section {
    margin-right: 0;
    margin-top: 100px;
    padding: 20px;
    width: 100%;
    box-sizing: border-box;
  }

  .skills-category {
    padding-right: 20px;
  }

  .skills-icons {
    flex-wrap: wrap;
    gap: 15px;
    justify-content: flex-start;
  }

  .skills-icons img {
    width: 40px;
    height: 40px;
  }

  .animation-container {
    display: none;
  }

  .skills-category h2 {
    min-width: 100px;
    font-size: 16px;
    margin-bottom: 0;
  }

  .skills-title {
    font-size: 28px;
    margin: 15px 0;
  }

  .skills-button {
    font-size: 13px;
    padding: 6px 14px;
  }
}