@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&family=Rubik+Vinyl&family=Source+Code+Pro:ital,wght@0,200..900;1,200..900&display=swap');

body {
    background-color: white;
    color: black;
    margin: 100;
    font-family: Arial, sans-serif;
    margin-left: 50px;
}

.container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    height: 100vh;
    margin-left: 100px;
    padding-bottom: -10px; 
}

.name {
    font-size: 6rem;
    background: linear-gradient(135deg, #8a4af3, #ff6f61);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    text-fill-color: transparent;
    margin: 0;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
    letter-spacing: 2px;
    font-weight: 800;
    font-family: 'Rubik Vinyl', sans-serif;
}

.surname {
    font-size: 4.5rem;
    background: linear-gradient(135deg, #ff6f61, #8a4af3);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    text-fill-color: transparent;
    margin: 0.5rem 0;
    position: relative;
    display: inline-block;
    font-style: italic;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
    font-weight: 600;
    font-family: 'Rubik Vinyl', sans-serif;
}

.description {
    font-size: 2rem;
    color: black;
    text-align: left;
    margin: 0.5rem 0;
    position: relative;
}

.description strong {
    font-weight: bold;
    color: purple;
    display: inline-block;
    position: relative;
    top: 0px;
}

.dynamic-text {
    display: inline-block;
    animation: text-change 6s linear infinite;
}

.social-icons {
    display: flex;
    gap: 1rem;
    margin: 1rem 0;
}

.icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border: 1px solid white;
    border-radius: 30%;
    text-decoration: none;
    color: #ffffff;
    font-size: 1.2rem;
}

.icon img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.button {
    margin-top: 1rem;
    padding: 0.8rem 2rem;
    font-size: 1rem;
    color: #000;
    background-color: #fff;
    border: 2px solid black;
    border-radius: 30px;
    cursor: pointer;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
}

.button:hover {
    background-color: #8a4af3;
    color: #fff;
}

.resume-icon {
    width: 30px;
    height: 25px;
    margin-right: 5px;
    margin-bottom: -6px;
}

.animation-container {
    position: relative;
    margin-top: -300px;  
    right: 20px;
    top: 30%;
    transform: translateY(-50%);
    width: 600px;
    height: auto;
}

/* Mobile Styles */
@media (max-width: 768px) {
    body {
        
        padding-left: 0px;
    }

    .container {
        margin-right: 120px;
        padding-left: 45px;
        padding-bottom: 40px;
        align-items: center;
        text-align: center;
    }

    .name {
        font-size: 3rem;
    }

    .surname {
        font-size: 2.5rem;
    }

    .description {
        font-size: 1.5rem;
        text-align: center;
    }

    .social-icons {
        justify-content: center;
    }

    .icon {
        width: 35px;
        height: 35px;
    }

    .button {
        margin: 1rem auto;
        padding: 0.6rem 1.5rem;
        font-size: 0.9rem;
    }

    .animation-container {
        display: none;
    }
}