/* Desktop Navigation */
.navigation-container,
.desktop-nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 260px;
  height: 60px;
  margin: 0 auto;
  border: 2px solid #000;
  border-radius: 30px;
  background-color: #fff;
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 100;
}

.icon-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #fff;
  border: 2px solid transparent;
  transition: all 0.3s ease;
}

.icon-wrapper:hover {
  border-color: #000;
  background-color: #c580f03c;
  transform: scale(1.1);
}

.nav-icon {
  color: #000;
  transition: all 0.3s ease;
}

.icon-wrapper:hover .nav-icon {
  transform: translateY(-2px);
}

/* Mobile Navigation */
.mobile-nav {
  display: none;
}

.mobile-nav-toggle,
.hamburger {
  position: fixed;
  top: 20px;
  left: 20px;
  width: 44px;
  height: 44px;
  border: 2px solid #000;
  border-radius: 8px;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1001;
  cursor: pointer;
  transition: all 0.3s ease;
}

.mobile-menu {
  position: fixed;
  top: 0;
  left: -250px;
  width: 250px;
  height: 100vh;
  background-color: white;
  transition: all 0.3s ease;
  z-index: 1000;
  box-shadow: 2px 0 10px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.mobile-menu.open {
  left: 0;
}

.mobile-nav-content {
  padding: 80px 20px 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.mobile-menu a {
  display: block;
  padding: 15px;
  color: #000;
  text-decoration: none;
  font-size: 1.2rem;
  transition: background-color 0.3s ease;
  border-radius: 8px;
}

.mobile-menu a:hover {
  background-color: #c580f03c;
}

/* Media Queries */
@media screen and (max-width: 768px) {
  .desktop-nav,
  .navigation-container {
    display: none;
  }
  
  .mobile-nav {
    display: block;
  }
}