@import url('https://fonts.googleapis.com/css2?family=Kanit&family=Poppins:wght@100;200;300;400;500;600;700&family=Noto+Sans:ital,wght@0,100..900;1,100..900&display=swap');

body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background: radial-gradient(#e5e7eb 1px, transparent 1px) white;
  background-size: 16px 16px;
  color: #333;
  overflow: hidden;
}

@keyframes float {
  0% { transform: translateY(0px); }
  50% { transform: translateY(-15px); }
  100% { transform: translateY(0px); }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.about-page {
  display: flex;
  justify-content: space-between;
  padding: 40px 20px;
  animation: fadeIn 1.5s ease-in-out;
  margin-left: 100px;
}

.left-section {
  width: 50%;
  padding-right: 30px;
  padding-top: 90px;
}

.right-section {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: float 3s ease-in-out infinite;
}

.about-button {
  display: flex;
  align-items: center;
  background: black;
  color: white;
  padding: 5px 30px;
  border-radius: 20px;
  width: fit-content;
  margin-bottom: 20px;
  font-family: 'Poppins', sans-serif;
  font-size: 0.9rem;
}

.about-icon {
  margin-right: 5px;
}

.title {
  font-size: 50px;
  font-weight: bold;
  margin-bottom: 0px;
  font-family: 'Kanit', sans-serif;
}

.subtitle {
  font-size: 39px;
  color: #555;
  margin-top: 3px;
}

.highlight {
  background: linear-gradient(to right, #833be7cb, #5521c5);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.underline {
  width: 200px;
  height: 10px;
  background: linear-gradient(to right, #c580f0ed, #c580f0c8);
  margin: 10px 0 20px;
  border-radius: 5px;
}

.custom-box {
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  text-align: justify;
  font-family: 'Poppins', sans-serif;
  margin-top: 20px;
  max-width: 600px;
  line-height: 1.6;
}

.custom-box-text {
  font-size: 1rem;
  line-height: 1.5;
  color: #444;
}

.dynamic-title {
  font-size: 48px;
  color: #000;
  font-family: 'Kanit', sans-serif;
}

.animation-container {
  width: 100%;
  height: auto;
  animation: fadeIn 2s ease-in-out, float 3s infinite ease-in-out;
}

/* Mobile Styles */
@media (max-width: 768px) {
  body {
    overflow-y: auto;
    margin: 0;
    padding: 30px;
  }

  .about-page {
    flex-direction: column;
    padding: 20px;
    margin-left: -50px;
  }

  .left-section {
    width: 100%;
    padding: 10px;
    text-align: center;
  }

  .right-section {
    width: 100%;
    padding: 10px;
    margin: 20px auto;
    animation: float 3s ease-in-out infinite;
  }

  .about-button {
    margin: 15px auto;
    padding: 10px 20px;
    font-size: 0.85rem;
  }

  .title {
    font-size: 32px;
  }

  .subtitle {
    font-size: 24px;
    margin-top: 10px;
  }

  .dynamic-title {
    font-size: 28px;
  }

  .underline {
    width: 150px;
    height: 6px;
    margin: 15px auto;
  }

  .custom-box {
    width: 100%;
    margin: 10px auto;
    padding: 15px;
  }

  .custom-box-text {
    font-size: 0.9rem;
  }
}
